  const generateEditor = () => {
    return `<?php
namespace ${vendorName}\\${moduleName}\\Block\\Adminhtml\\System\\Config;
 
use Magento\\Backend\\Block\\Template\\Context;
use Magento\\Cms\\Model\\Wysiwyg\\Config as WysiwygConfig;
use Magento\\Framework\\Data\\Form\\Element\\AbstractElement;
 
class Editor extends \\Magento\\Config\\Block\\System\\Config\\Form\\Field
{ 
    /**
     * @param Context       $context
     * @param WysiwygConfig $wysiwygConfig
     * @param array         $data
     */
    public function __construct(
        Context $context,
        WysiwygConfig $wysiwygConfig,
        array $data = []
    ) {
        $this->_wysiwygConfig = $wysiwygConfig;
        parent::__construct($context, $data);
    }
 
    protected function _getElementHtml(\\Magento\\Framework\\Data\\Form\\Element\\AbstractElement $element)
    {
        // set wysiwyg for element
        $element->setWysiwyg(true);
        // set configuration values 
        $element->setConfig($this->_wysiwygConfig->getConfig($element));
        return parent::_getElementHtml($element);
    }
}`;
  };
  const generateColorPickerXml = () => {
    return `<?xml version="1.0" encoding="UTF-8"?>
<page xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" layout="admin-2columns-left" xsi:noNamespaceSchemaLocation="../../../../../../../lib/internal/Magento/Framework/View/Layout/etc/page_configuration.xsd">
    <head>
        <css src="jquery/colorpicker/css/colorpicker.css"/>
        <link src="jquery/colorpicker/js/colorpicker.js"/>
    </head>
</page>`;
  };
  const generateColorPicker = () => {
    return `<?php
namespace ${vendorName}\\${moduleName}\\Block\\Adminhtml\\System\\Config;

class ColorPicker extends \\Magento\\Config\\Block\\System\\Config\\Form\\Field
{
  public function __construct(
      \\Magento\\Backend\\Block\\Template\\Context $context, array $data = []
    ) {
          parent::__construct($context, $data);
    }
 
  protected function _getElementHtml(\\Magento\\Framework\\Data\\Form\\Element\\AbstractElement $element) {
      $html = $element->getElementHtml();
      $value = $element->getData('value');
 
      $html .= '<script type="text/javascript">
            require(["jquery","jquery/colorpicker/js/colorpicker"], function ($) {
                $(document).ready(function () {
                  var $el = $("#' . $element->getHtmlId() . '");
                    $el.css("backgroundColor", "'. $value .'");
 
                  // Attach the color picker
                    $el.ColorPicker({
                      color: "'. $value .'",
                        onChange: function (hsb, hex, rgb) {
                            $el.css("backgroundColor", "#" + hex).val("#" + hex);
                      }
                  });
              });
          });
          </script>';
      return $html;
  }
}`;
  };
  const generateDateTimePicker = () => {
    return `<?php
namespace ${vendorName}\\${moduleName}\\Block\\Adminhtml\\System\\Config;

class DateTimePicker extends \\Magento\\Config\\Block\\System\\Config\\Form\\Field
{
    /**
     * Render function
     *
     * @param \\Magento\\Framework\\Data\\Form\\Element\\AbstractElement $element
     */
    public function render(\\Magento\\Framework\\Data\\Form\\Element\\AbstractElement $element)
    {
        $element->setDateFormat(\\Magento\\Framework\\Stdlib\\DateTime::DATE_INTERNAL_FORMAT);
        $element->setTimeFormat('HH:mm:ss');
        $element->setShowsTime(true);
        return parent::render($element);
    }
}`;
  };
