    const buildTreeData = (paths) => {
    const root = {};
    paths.forEach(({ path }) => {
      path = vendorName+"_"+moduleName+"/" + path;
      const parts = path.split("/");
      let current = root;
      parts.forEach((part, index) => {
        if (!current[part]) {
          current[part] = index === parts.length - 1 ? null : {};
        }
        current = current[part];
      });
    });
    const customSort = (keyA, keyB) => {
      if (keyA[0] === keyB[0]) return 0;
      if (keyA[0] < keyB[0]) return -1;
      return 1;
    };
    const convertToTreeData = (node, parentKey = "") =>
      Object.entries(node)
      .sort(([keyA], [keyB]) => customSort(keyA, keyB))
      .map(([key, value]) => ({
        title: key,
        key: parentKey ? `${parentKey}/${key}` : key,
        children: value ? convertToTreeData(value, parentKey ? `${parentKey}/${key}` : key) : null,
      }));
    return convertToTreeData(root);
  };
  const treeData = validFieldExists && buildTreeData(moduleStructure);
